import Vue from 'vue'
import VueRouter from 'vue-router'
import Add from './add'
import List from './list'

// import 'assets/a.css'
require('common/css/reset.css')

Vue.use(VueRouter)

// 
// 
const router = new VueRouter({
  mode: 'history',
  // base: baseUrl,
  routes: [
    {
      path: '/add',
      component: Add
    },
    {
      path: '/list',
      component: List
    }
  ]
})

// 
//  App  #index 
/* eslint-disable no-new */
new Vue({
  router: router
}).$mount('#index')



